<template>
  <div class="col-md-9 left-col pull-right">
    <div class="panel article-body content-body">
      <h1 class="text-center">{{ title }}</h1>
      <div class="article-meta text-center">
        <i class="fa fa-clock-o"></i>
        <abbr>{{ date | moment('from') }}</abbr>
      </div>
      <div class="entry-content">
        <div class="content-body entry-content panel-body ">
          <div class="markdown-body" v-html="content"></div>

          <div v-if="auth && uid === 1" class="panel-footer operate">
            <div class="actions">
              <a @click="deleteArticle" class="admin" href="javascript:;"><i class="fa fa-trash-o"></i></a>
              <a @click="editArticle" class="admin" href="javascript:;"><i class="fa fa-pencil-square-o"></i></a>
            </div>
          </div>
        </div>
      </div>
    </div>
	<!--  -->
	<div class="votes-container panel panel-default padding-md">
	  <div class="panel-body vote-box text-center">
	    <div class="btn-group">
	      <a @click="like" href="javascript:;" class="vote btn btn-primary popover-with-html" :class="likeClass">
	        <i class="fa fa-thumbs-up"></i> {{ likeClass ? '' : '' }}
	      </a>
	      <div class="or"></div>
	      <button @click="showQrcode = true" class="btn btn-success"><i class="fa fa-heart"></i> </button>
	    </div>
	    <div class="voted-users">
	      <div class="user-lists">
	        <span v-for="likeUser in likeUsers">
	          <!--  animated  swing   -->
	          <img :src="user && user.avatar" class="img-thumbnail avatar avatar-middle" :class="{ 'animated swing' : likeUser.uid === 1 }">
	        </span>
	      </div>
	      <div v-if="!likeUsers.length" class="vote-hint"> ?</div>
	    </div>
	  </div>
	</div>
	
	<!--  -->
	<Modal :show.sync="showQrcode" class="text-center">
	  <template v-slot:header>
	    <div v-if="user">
	      <img :src="user.avatar" class="img-thumbnail avatar" width="48">
	    </div>
	  </template>
	  <div>
	    <p class="text-md">Learnku Vue.js.com </p>
	    <div class="payment-qrcode inline-block">
	      <h5> Learnku Vue.js.com</h5>
	      <p><qrcode-vue value="https://learnku.com/vuejs/" :size="160"></qrcode-vue></p>
	    </div>
	  </div>
	  <template v-slot:footer>
	    <div>
	      <div class="text-center"> :)</div>
	    </div>
	  </template>
	</Modal>
  </div>
</template>

<script>
import SimpleMDE from 'simplemde'
import hljs from 'highlight.js'
import emoji from 'node-emoji'
import { mapState } from 'vuex'
import QrcodeVue from 'qrcode.vue'

export default {
  name: 'Content',
  components: {
    QrcodeVue
  },  
  data() {
    return {
      title: '', // 
      content: '', // 
      date: '', // 
      uid: 1 ,//  ID
	  likeUsers: [], // 
	  likeClass: '', // 
	  showQrcode: false, // 
    }
  },
  computed: {
    ...mapState([
      'auth',
      'user'
    ])
  },
  created() {
    const articleId = this.$route.params.articleId
    const article = this.$store.getters.getArticleById(articleId)
  
    if (article) {
      //  likeUsers
      let { uid, title, content, date, likeUsers } = article
  
      this.uid = uid
      this.title = title
      this.content = SimpleMDE.prototype.markdown(emoji.emojify(content, name => name))
      this.date = date
      //  likeUsers
      this.likeUsers = likeUsers || []
      //  likeClass active
      this.likeClass = this.likeUsers.some(likeUser => likeUser.uid === 1) ? 'active' : ''
  
      this.$nextTick(() => {
        this.$el.querySelectorAll('pre code').forEach((el) => {
          hljs.highlightBlock(el)
        })
      })
    }
  
    this.articleId = articleId
  },
  methods: {
    editArticle() {
      this.$router.push({ name: 'Edit', params: { articleId: this.articleId } })
    },
    deleteArticle() {
      this.$swal({
        text: '?',
        confirmButtonText: ''
      }).then((res) => {
        if (res.value) {
          this.$store.dispatch('post', { articleId: this.articleId })
        }
      })
    },
	like(e) {
	  // 
	  if (!this.auth) {
	    this.$swal({
	      text: '',
	      confirmButtonText: ''
	    }).then((res) => {
	      if (res.value) {
	        this.$router.push('/auth/login')
	      }
	    })
	  } else {
	    const target = e.currentTarget
	    //  active 
	    const active = target.classList.contains('active')
	    const articleId = this.articleId
	
	    if (active) {
	      // 
	      this.likeClass = ''
	      //  like  likeUsers 
	      this.$store.dispatch('like', { articleId }).then((likeUsers) => {
	        this.likeUsers = likeUsers
	      })
	    } else {
	      // 
	      this.likeClass = 'active animated rubberBand'
	      //  like  isAdd  likeUsers 
	      this.$store.dispatch('like', { articleId, isAdd: true }).then((likeUsers) => {
	        this.likeUsers = likeUsers
	      })
	    }
	  }
	},
  }
}
</script>

